"use client";

import Image from "next/image";
import { motion } from "framer-motion";
import { SearchBox } from "./SearchBox";

export function Hero() {
    return (
        <div className="relative w-full h-[85vh] flex flex-col items-center justify-center">
            {/* Background Image */}
            <div className="absolute inset-0 z-0">
                <Image
                    src="/images/hero-taj.png"
                    alt="Taj Mahal at Sunrise"
                    fill
                    className="object-cover"
                    priority
                    quality={100}
                />
                {/* Gradient Overlay */}
                <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-black/20 to-black/30" />
            </div>

            {/* Content */}
            <div className="relative z-10 container mx-auto px-4 text-center mt-20">
                <motion.h1
                    className="text-4xl md:text-6xl lg:text-7xl font-bold font-display text-white mb-6 drop-shadow-lg"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8, ease: "easeOut" }}
                >
                    Discover the Soul of <span className="text-accent">India</span>
                </motion.h1>
                <motion.p
                    className="text-lg md:text-xl text-gray-200 mb-8 max-w-2xl mx-auto font-light"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8, delay: 0.2, ease: "easeOut" }}
                >
                    From the misty Himalayas to the golden sands of Kerala,
                    experience luxury journeys curated just for you.
                </motion.p>
            </div>

            {/* Search Box Container - positioned absolutely at bottom to overlap or just below content */}
            <div className="absolute top-[80%] w-full z-20 px-4">
                <SearchBox />
            </div>
        </div>
    );
}
