"use client";

import { useState } from "react";
import { Search, Calendar, MapPin, Users } from "lucide-react";
import { motion } from "framer-motion";
import { cn } from "@/lib/utils";

export function SearchBox() {
    const [activeTab, setActiveTab] = useState("packages"); // packages, hotels, flights

    return (
        <div className="bg-white dark:bg-gray-800 rounded-2xl shadow-xl p-6 md:p-8 max-w-4xl w-full mx-auto -mt-24 relative z-20 border border-gray-100 dark:border-gray-700">
            {/* Tabs */}
            <div className="flex space-x-6 border-b border-gray-100 dark:border-gray-700 pb-4 mb-6">
                <button
                    onClick={() => setActiveTab("packages")}
                    className={cn(
                        "text-sm font-semibold pb-2 border-b-2 transition-all",
                        activeTab === "packages"
                            ? "border-primary text-primary"
                            : "border-transparent text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
                    )}
                >
                    Holiday Packages
                </button>
                <button
                    onClick={() => setActiveTab("hotels")}
                    className={cn(
                        "text-sm font-semibold pb-2 border-b-2 transition-all",
                        activeTab === "hotels"
                            ? "border-primary text-primary"
                            : "border-transparent text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
                    )}
                >
                    Hotels
                </button>
            </div>

            {/* Input Fields */}
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4 divide-y md:divide-y-0 md:divide-x divide-gray-100 dark:divide-gray-700">
                <div className="flex flex-col space-y-1 md:pr-4">
                    <label className="text-xs uppercase font-bold text-gray-400 tracking-wider">
                        Destination
                    </label>
                    <div className="flex items-center space-x-2">
                        <MapPin className="w-5 h-5 text-gray-400" />
                        <input
                            type="text"
                            placeholder="Where to?"
                            className="w-full font-semibold text-gray-800 dark:text-gray-100 placeholder-gray-300 dark:placeholder-gray-500 bg-transparent focus:outline-none"
                        />
                    </div>
                </div>

                <div className="flex flex-col space-y-1 md:px-4">
                    <label className="text-xs uppercase font-bold text-gray-400 tracking-wider">
                        Date
                    </label>
                    <div className="flex items-center space-x-2">
                        <Calendar className="w-5 h-5 text-gray-400" />
                        <input
                            type="text"
                            placeholder="Add dates"
                            className="w-full font-semibold text-gray-800 dark:text-gray-100 placeholder-gray-300 dark:placeholder-gray-500 bg-transparent focus:outline-none"
                        />
                    </div>
                </div>

                <div className="flex flex-col space-y-1 md:px-4">
                    <label className="text-xs uppercase font-bold text-gray-400 tracking-wider">
                        Travelers
                    </label>
                    <div className="flex items-center space-x-2">
                        <Users className="w-5 h-5 text-gray-400" />
                        <input
                            type="number"
                            min="1"
                            defaultValue="2"
                            className="w-full font-semibold text-gray-800 dark:text-gray-100 placeholder-gray-300 dark:placeholder-gray-500 bg-transparent focus:outline-none"
                        />
                    </div>
                </div>

                <div className="flex items-center justify-center md:pl-4 pt-4 md:pt-0">
                    <button className="bg-primary hover:bg-blue-700 text-white rounded-full w-full h-12 md:h-14 flex items-center justify-center font-bold text-lg shadow-lg shadow-blue-500/30 transition-all transform hover:scale-105">
                        Search
                    </button>
                </div>
            </div>
        </div>
    );
}
